.jumboStyle {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, #552A7F), to(#663399));
    background: -webkit-linear-gradient(top, #552A7F 20%, #663399 100%);
    background: -o-linear-gradient(top, #552A7F 20%, #663399 100%);
    background: linear-gradient(to bottom, #552A7F 20%, #663399 100%);
    color: gold;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.signButton {
    background: dodgerblue;
    border: none;
    margin: 10px;
}



