.textColor {
    color: gold;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(100%, #663399),color-stop(20%, #BB99CD));
    background: -webkit-linear-gradient(top, #663399 100%,#BB99CD 20%);
    background: -o-linear-gradient(top, #663399 100%,#BB99CD 20%);
    background: linear-gradient(to bottom, #663399 100%,#BB99CD 20%);
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    height: 50vh;
}


