.navColor {
    background: -webkit-gradient(linear, left top, right top, color-stop(20%, #552A7F), to(#663399));
    background: -webkit-linear-gradient(left, #552A7F 20%, #663399 100%);
    background: -o-linear-gradient(left, #552A7F 20%, #663399 100%);
    background: linear-gradient(to right, #552A7F 20%, #663399 100%);
}

.linkColor {
    padding: 0;
    margin: 0vh 5vw;
}

.bar:hover {
    color: red;
}

.bar::before {
    content: "";
    display: block;
    height: 5px;
    background-color: white;
    position: absolute;
    top: 0;
    width: 0%;
    -webkit-transition: all ease-in-out 500ms;
    -o-transition: all ease-in-out 500ms;
    transition: all ease-in-out 500ms;
}

.bar:hover::before {
    width: 5vw
}


